.container {
  max-width: 800px;
  margin: 0 auto;
  padding: 60px 30px;

  display: grid;
  grid-template-columns: 2fr 1fr;
  grid-template-rows: auto 1fr 1fr;
  -webkit-animation: fadein 1.25s;
          animation: fadein 1.25s;
}
@-webkit-keyframes fadein {
  from {
    opacity: 0;
  }
  to {
    opacity: 1;
  }
}
@keyframes fadein {
  from {
    opacity: 0;
  }
  to {
    opacity: 1;
  }
}

.user-info {
  grid-area: user-info;
}

.photo {
  grid-area: photo;
}

.about {
  grid-area: about;
}

.work-exps {
  grid-area: work-exps;
}
.education {
  grid-area: education;
}

.knowlodges {
  grid-area: knowlodges;
}

.social-media {
  grid-area: social-media;
}
.container {
  grid-template-areas:
    "user-info photo"
    "work-exps about"
    "education knowlodges";
}

@media (max-width: 800px) {
  .container {
    grid-gap: 0px;
    grid-template-columns: 1fr;
